(function() {
  this.App.service('MnoeTenant', ["MnoeAdminApiSvc", function(MnoeAdminApiSvc) {
    var _self;
    _self = this;
    this.get = function() {
      return MnoeAdminApiSvc.one('tenant').get();
    };
    this.update = function(config) {
      return MnoeAdminApiSvc.one('tenant').patch({
        tenant: {
          frontend_config: config
        }
      });
    };
    return this;
  }]);

}).call(this);
