(function() {
  this.App.controller('SettingsController', ["$timeout", "$window", "CONFIG_JSON_SCHEMA", "MnoeTenant", function($timeout, $window, CONFIG_JSON_SCHEMA, MnoeTenant) {
    'ngInject';
    var vm;
    vm = this;
    vm.settingsModel = {};
    vm.settingsSchema = CONFIG_JSON_SCHEMA;
    vm.settingsForm = [
      {
        type: "tabs",
        tabs: [
          {
            title: "System",
            items: ["system"]
          }, {
            title: "Dashboard",
            items: ["dashboard"]
          }, {
            title: "Admin Panel",
            items: ["admin_panel"]
          }
        ]
      }, {
        type: "actions",
        items: [
          {
            type: "submit",
            title: "Save"
          }
        ]
      }
    ];
    MnoeTenant.get().then(function(response) {
      return vm.settingsModel = response.data.frontend_config;
    });
    vm.saveSettings = function(form) {
      return MnoeTenant.update(vm.settingsModel).then(function() {
        return $timeout(function() {
          return $window.location.reload();
        }, 2000);
      }, function() {
        return console.log("Error");
      });
    };
  }]);

}).call(this);
